<Stop current={stop.isCurrentStop}>
    <span class="mdi mdi-{icon}"></span>
    {stop.TimingPointName}
</Stop>

<script>
// Components
import Stop from "@/components/RouteStep/Stop.svelte";

// Props
export var stop;

// Computed
$: icon = getIcon(stop);

// Functions
function getIcon(stop) {
    var status = stop.TripStopStatus;

    if (status === "PASSED") return "check-all";
    else if (status === "ARRIVED") return "check";
    else return "alarm";
}
</script>