<InfoRow>
    <span slot="left">{startTimeString}</span>
    <span slot="center">
        <HeadSign icon="bus" {backgroundColor} {textColor}>{line.short_name}</HeadSign>
    </span>
    <span slot="right">{endTimeString}</span>
</InfoRow>
<InfoRow>
    <span slot="left"><span class="mdi mdi-arrow-expand-right"></span> {transitDetails.departure_stop.name}</span>
</InfoRow>
<InfoRow>
    <span slot="left"><span class="mdi mdi-arrow-collapse-right"></span> {transitDetails.arrival_stop.name}</span>
</InfoRow>
<InfoRow>
    <span slot="left"><span class="mdi mdi-rss"></span> {currentStopText}</span>
</InfoRow>
<DropDown>
    <InfoRow>
        <span slot="left"><span class="mdi mdi-arrow-left-right"></span> {transitDetails.line.name}</span>
    </InfoRow>
    <!-- <BusTimeline {stops} /> -->
    {#each activeStops as stop}
        <BusStop {stop} />
    {/each}
</DropDown>

<script>
import { onMount, onDestroy } from "svelte";
import { getBusStop, findDeparture, getBusStopsFromJourney, getBusTrip, getActiveStops, getCurrentStop } from "@/js/busUtils.js";
import { getStepColor, getStepTextColor, createStepStore } from "@/js/stepUtils.js";
import { standardizeTimestamp, toTimeString } from "@/js/timeUtils.js";

// Components
import InfoRow from "@/components/InfoRow.svelte";
import HeadSign from "@/components/HeadSign.svelte";
import DropDown from "@/components/DropDown.svelte";
import BusStop from "@/components/RouteStep/BusStop.svelte";
import BusTimeline from "@/components/RouteStep/BusTimeline.svelte";

// Props
export var step;

// Data
const maxTimeAfterStart = 15 * 60 * 1000; // 15 minutes. The time after which the app should stop refreshing the data because it's eather invalid or non existent
const refreshTime = 15 * 1000; // 30 seconds. The interfal at which the app will ping the server for updates after it found the trip
const checkInterval = 30 * 1000; // 30 seconds. Delay the checks should wait before trying again.
const checkStartBuffer = 5 * 60 * 1000; // 5 minutes. How many minutes before the expected departute time it should checking.
const checkEndBuffer = 5 * 50 * 1000; // 5 minutes. How many minutes it should keep checking after the exptected departure time.

var stepStore = createStepStore(step.id, {});
$stepStore.maxAge = 15 * 60 * 1000; // 15 minutes;
$stepStore.timestamp = Date.now();

var refreshTripTimer = setInterval(refreshBusJourney, refreshTime);
var busStop;
var busJourney;
var busJourneyFoundTimestamp;

// Computed
$: transitDetails = step.transit_details;
$: line = transitDetails.line;
$: backgroundColor = getStepColor("bus", transitDetails);
$: textColor = getStepTextColor("bus", transitDetails);
$: startTimestamp = standardizeTimestamp(transitDetails.departure_time.value);
$: startTimeString = toTimeString(startTimestamp);
$: endTimestamp = standardizeTimestamp(transitDetails.arrival_time.value);
$: endTimeString = toTimeString(endTimestamp);
$: busJourneyFound = $stepStore.busJourneyCode !== undefined;
$: stops = getBusStopsFromJourney(busJourney);
$: activeStops = getActiveStops(stops);
$: currentStop = getCurrentStop(stops);
$: currentStopText = getCurrentStopText(currentStop);
$: console.log($stepStore, busJourneyFound);

// Functions
async function refreshBusJourney() {
    if (Date.now() - busJourneyFoundTimestamp > maxTimeAfterStart) {
        if (refreshTripTimer) clearInterval(refreshTripTimer);
        return;
    }

    if (busJourneyFound) {
        busJourney = await getBusTrip($stepStore.busJourneyCode);
    }
}

function getCurrentStopText(currentStop) {
    if (!currentStop) return "Geen live data";

    if (currentStop.TripStopStatus === "ARRIVED") {
        return `Aangekomen op ${currentStop.TimingPointName}`;
    } else { // Driving
        return `Rijd naar ${currentStop.TimingPointName}`;
    }
}

// On create
onMount(async () => {
    // Get the location of the busstop and meta data from server
    var { lat, lng } = step.start_location;
    busStop = await getBusStop(lat, lng);
    
    // Early exit if the journeyCode is found in local storage
    if (busJourneyFound) {
        busJourneyFoundTimestamp = Date.now();

        // Do an initial check
        refreshBusJourney();
    }

    // Recursive function for finding the departure
    const checkDeparture = delay => {
        setTimeout(async () => {
            if (Date.now() - startTimestamp > checkEndBuffer) return false;

            busStop = await getBusStop(lat, lng);
            var departures = findDeparture(step.transit_details.departure_time.value, line.short_name, busStop.Passes);

            if (departures.length > 0) {
                $stepStore.busJourneyCode = departures[0].key;
                busJourneyFoundTimestamp = Date.now();
                queueMicrotask(refreshBusJourney);
            } else {
                checkDeparture(checkInterval);
            }
        }, delay);
    }

    var now = Date.now();
    var startCheckAfter = startTimestamp - now - checkStartBuffer;

    // Do a check now if there is a wait and start the timer
    if (startCheckAfter > 0) checkDeparture(0);
    checkDeparture(startCheckAfter);
});

// On destroy
onDestroy(() => {
    clearInterval(refreshTripTimer);
});
</script>