export function toTimeString(timestamp) {
    timestamp = standardizeTimestamp(timestamp);

    var d = new Date(timestamp);
    var hours = toFixedLength(d.getHours());
    var minutes = toFixedLength(d.getMinutes());

    return `${hours}:${minutes}`;
}

export function standardizeTimestamp(timestamp) {
    var isISO = timestamp > 2000000000;

    if (isISO) {
        return timestamp;
    } else {
        return timestamp * 1000;
    }
}

export function toFixedLength(value) {
    if (value < 10) {
        return "0" + value;
    } else {
        return String(value);
    }
}