<div class="addressRouteHeader" on:click={onClick}>
    <div class="iconWrapper">
        <span class="mdi mdi-{address.icon}"></span>
    </div>
    <div class="textWrapper">
        <p class="name">{address.name}</p>
        <p class="address">{address.address}</p>
    </div>
</div>

<script>
// Imports
import { createEventDispatcher } from "svelte";
const dispatch = createEventDispatcher();

// Props
export var address = {};

// Functions
function onClick() {
    dispatch("click");
}
</script>

<style lang="scss">

.addressRouteHeader {
    height: 48px;
    width: 100%;

    display: flex;
    flex-direction: row;

    .iconWrapper {
        width: 48px;

        flex-grow: 0;
        flex-shrink: 0;

        span {
            position: relative;
            display: block;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);

            text-align: center;
            font-size: 24px;
        }
    }

    .textWrapper {
        position: relative;
        flex-grow: 1;
        flex-shrink: 1;

        overflow: hidden;

        p {
            width: 100%;

            margin: 0;

            &.name {
                position: absolute;
                top: 4px;

                font-weight: 700;
                font-size: 18px;
            }

            &.address {
                position: absolute;
                bottom: 4px;

                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
            }
        }
    }
}

</style>