import { writableLinked } from "@/js/storeUtils.js";
import { locationStore, getDirectDistance } from "@/js/locationUtils.js";

export const addressStore = writableLinked("addresses", {
    home: {
        name: "Huis",
        icon: "home-outline",
        canBeDeleted: false,
        address: "",
        location: {
            lat: 0,
            long: 0,
        },
    },
    work: {
        name: "Werk",
        icon: "briefcase-outline",
        canBeDeleted: false,
        address: "",
        location: {
            lat: 0,
            long: 0,
        },
    },
});

const autoAddress = {
    name: "Auto",
    id: "auto",
    icon: "arrow-decision-auto-outline",
    canBeDeleted: false,
    get address() {
        return getAutoLocation().address + " (auto)";
    },
    get location() {
        return getAutoLocation().location;
    }
}

const currentLocationAddress = {
    name: "Huidige locatie",
    id: "currentLocation",
    icon: "crosshairs-gps",
    canBeDeleted: false,
    address: "Gebruik GPS locatie",
    location: {
        lat: 0,
        long: 0,
    },
}

const defaultAddress = {
    name: "Nieuw addres",
    id: "invalid",
    icon: "crosshairs-gps",
    canBeDeleted: true,
    address: "",
    location: {
        lat: 0,
        long: 0,
    },
}

export function saveAddress(id, object) {
    object = {
        ...defaultAddress,
        ...object,
        id,
    }

    let storeContent = addressStore.get();
    storeContent[id] = object;

    addressStore.set(storeContent);
}

export function getAddressById(id) {
    if (id === "auto") {
        return getAutoLocation();
    } else if (id === "currentLocation") {
        let instance = { ...currentLocationAddress };
        instance.location = locationStore.get();

        return instance;
    } else {
        let addresses = addressStore.get();
        let match = addresses[id];

        return match;
    }
}

export function getAutoLocation() {
    var addresses = addressStore.get();
    var home = addresses.home;
    var work = addresses.work

    home.id = "home";
    work.id = "work";

    var homeLocation = home.location;
    var workLocation = work.location;
    var currentLocation = locationStore.get();

    var directDistanceHome = getDirectDistance(homeLocation, currentLocation);
    var directDistanceWork = getDirectDistance(workLocation, currentLocation);

    return directDistanceHome < directDistanceWork ? addresses.work : addresses.home;
}

export function getAddresses() {
    var addresses = addressStore.get();

    return Object.entries(addresses)
        .map(([key, value]) => {
            return {
                canBeDeleted: true,
                id: key,
                ...value,
            }
        });
}

export function getCompleteAddressMap() {
    return {
        ...addressStore.get(),
        currentLocation: currentLocationAddress,
        auto: autoAddress,
    }
}

export function getOriginAddresses() {
    return [
        ...getAddresses(),
        currentLocationAddress,
    ]
}

export function getDestinationAddresses() {
    return [
        ...getAddresses(),
        autoAddress,
    ]
}

export function matchLocationFromId(id) {
    var address = getAddressById(id);
    if (address) return address.location;
}