<svelte:component this={currentLayout}>
    <div class="trip {openClass}">
        <InfoRow>
            <span slot="left">{endName}</span>
        </InfoRow>
        <InfoRow>
            <span slot="left">
                {departureTimeString}
            </span>
            <span class="tripIcons" slot="center">
                {#each icons as icon}
                    <span class="mdi mdi-{icon}"></span>
                {/each}
            </span>
            <span slot="right">
                {arrivalTimeString}
            </span>
        </InfoRow>
        <TripOverviewBar {steps} />
        <div class="buttons">
            <SoftButton on:click="{() => loadTrip(trip)}" icon="play-circle-outline">
                Start
            </SoftButton>
            <SoftButton icon={toggleOpenButtonIcon} on:click={toggleOpen}>
                {#if !open}
                    Meer Info
                {:else}
                    Minder Info
                {/if}
            </SoftButton>
        </div>
        {#if open}
            <div class="tripSteps">
                {#each steps as step}
                    <StepSmall {step} />
                {/each}
            </div>
        {/if}
    </div>
</svelte:component>

<script>
import { standardizeTimestamp, toTimeString } from "@/js/timeUtils.js";
import { getStepIcon } from "@/js/stepUtils.js";
import { processSteps, startRoute } from "@/js/routeUtils.js";

// Components
import InfoRow from "@/components/InfoRow.svelte";
import TripOverviewBar from "@/components/TripOverviewBar.svelte";
import SoftButton from "@/components/SoftButton.svelte";
import StepSmall from "@/components/StepSmall.svelte";
import TripStepDetail from "@/components/TripStepDetail.svelte";
import LayoutCenter from "@/components/Layout/LayoutCenter.svelte";
import LayoutExpanded from "@/components/Layout/LayoutExpanded.svelte";

// Props
export var trip;
console.log(trip);

// Data
var open = false;

// Computed
$: steps = trip.steps;
// $: leg = trip.legs[0];
// $: lastStep = leg.steps[leg.steps.length - 1];
$: endName = trip.toAddress.streetAddress;
// $: hasDepartureTime = leg.departure_time !== undefined;
// $: hasArrivalTime = leg.arrival_time !== undefined;
// $: departureTimetamp = hasDepartureTime ? standardizeTimestamp(leg.departure_time.value) : 0;
// $: arrivalTimetamp = hasArrivalTime ? standardizeTimestamp(leg.arrival_time.value) : 0;
// $:  = toTimeString(departureTimetamp);
$: arrivalTimeString = trip.arrivalTime.timeString;
$: departureTimeString = trip.departureTime.timeString;
// $: steps = processSteps(leg.steps);
$: openClass = open ? "open" : "";
// $: toggleOpenButtonIcon = !open ? "plus-circle-outline" : "minus-circle-outline"
$: icons = getTripIcons(steps);
$: currentLayout = open ? LayoutExpanded : LayoutCenter;

// Functions
function getEndName(leg) {
    var address = leg.end_address.split(", ");
    address.pop();
    address.pop();
    return address.join(", ");
}

function getTripIcons(steps) {
    return steps
        .map(step => getStepIcon(step));
}

function loadTrip(trip) {
    document.querySelector("main").scrollTo(0, 0);

    startRoute(trip);
}

function toggleOpen() {
    open = !open;
}
</script>

<style lang="scss">

.trip {
    padding: 8px 0;
    margin: 8px auto;
    box-sizing: border-box;

    &.open {
        // width: calc(95% - 32px);

        padding: 24px 16px;

        border-radius: 10px;
        box-shadow: var(--defaultBoxShadow);
    }

    .buttons {
        width: 100%;

        margin: 8px 0;

        display: inline-block;

        & > :global(button) {
            width: 50%;

            float: left;
        }
    }

    .tripSteps :global(.routeStep) {
        width: calc(100% - 16px);
    }
}

.tripIcons {
    font-size: 18px;
}

</style>