{#if enabled}
    <div class="busTimeline">
        {#each stops as stop}
            <div class="busSection" style="{getStyle(stop)}"></div>
        {/each}
        <div class="currentPosition">
            <span class="mdi mdi-chevron-right"></span>
        </div>
    </div>
{/if}

<script>
import { getCurrentStop } from "@/js/busUtils.js";

// Props
export var stops = [];

// Computed
$: enabled = stops.length > 0;
$: percentage = 100 / stops.length;
$: currentStop = getCurrentStop(stops);
$: console.log(stops);

// Functions
function getStyle(stop) {
    var color = "transparent";
    var status = stop.TripStopStatus;

    if (stop === currentStop) {
        color = "var(--stepBusCurrent)";
    } else if (status === "PASSED" || status === "ARRIVED") {
        color = "var(--stepBusPassed)";
    }

    return `width: ${percentage}%; background-color: ${color};`;
}
</script>

<style lang="scss">

.busTimeline {
    height: 2px;
    width: 100%;

    position: relative;
    margin: 16px auto;
    background-color: var(--lowContrastColor);

    .currentPosition {
        height: 16px;
        width: 16px;

        position: absolute;
        border-radius: 50%;
        top: 50%;
        transform: translateY(-50%);

        background-color: #750000;

        span {
            height: 100%;
            width: 100%;
            display: block;

            font-size: 14px;
            color: #ffffff;
            line-height: 16px;
            text-align: center;
        }
    }

    .busSection {
        height: 100%;
        
        float: left;
    }
}

</style>