<section class="trips">
    <LayoutCenter>
        {#if !showResults}
            <TripComposer on:search={onSearch} />
        {/if}
    </LayoutCenter>
    {#if showResults}
        <LayoutCenter>
            <Button on:click={redoSearch}>Zoek opnieuw</Button>
        </LayoutCenter>
        {#each trips as trip}
            <Trip {trip} />
        {:else}
            <LayoutCenter>
                <EmptyState>Geen routes gevonden</EmptyState>
            </LayoutCenter>
        {/each}
    {/if}
</section>

<script>
// Stores
import { availableTripsStore, startAutoPlanner, getTripFromAddress, getTripToAddress } from "@/js/tripUtils.js";
import { addressStore } from "@/js/addressUtils.js";

// Components
import Trip from "@/components/Trip.svelte";
import LayoutCenter from "@/components/Layout/LayoutCenter.svelte";
import EmptyState from "@/components/EmptyState.svelte";
import TripComposer from "@/components/TripComposer.svelte";
import Popup from "@/components/Popup.svelte";
import Button from "@/components/Button.svelte";

// Data
var showResults = false;
var trips = [];

// Computed
$: fromAddress = getTripFromAddress($availableTripsStore);
$: toAddress = getTripFromAddress($availableTripsStore);

// Function
function onSearch({ detail }) {
    showResults = true;
    trips = detail.trips;
}

function redoSearch() {
    showResults = false;
}

// On create
startAutoPlanner();
</script>

<style lang="scss">

section {
    padding-bottom: 16px;
}

.trips {
    width: 100%;

    margin: 0 auto;
    padding-top: 16px;

    header {
        position: relative;
        margin-bottom: 16px;

        button {
            height: 100%;

            position: absolute;
            top: 0;
            right: 0;
            margin: 0;
            padding: 0;

            background-color: transparent;
            border: 0;
            color: var(--textColor);
        }
    }
}

h3 {
    margin: 16px auto;
    margin-bottom: 4px;
}

h6 {
    margin: 0 auto;
    margin-bottom: 16px;

    font-size: 16px;
    font-weight: 100;
    color: var(--lowContrastColor);
}

</style>