<div class="spinner {center && "center"}"></div>

<script>
// Props
export var center = true;
export var image = false;
</script>

<style lang="scss">

.spinner {
    --color: #000000;
    --secondColor: #383838;
    --size: 32px;

    height: var(--size);
    width: var(--size);

    background-color: var(--color);
    border-radius: 50%;
    animation: spinner linear 500ms infinite;

    &.center {
        position: relative;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);

        animation: spinnerCenter linear 500ms infinite;
    }
}

@keyframes spinner {
    0% {
        background-color: var(--color);
        transform: scale(1);
    }

    50% {
        background-color: var(--secondColor);
        transform: scale(1.3);
    }

    100% {
        background-color: var(--color);
        transform: scale(1);
    }
}

@keyframes spinnerCenter {
    0% {
        background-color: var(--color);
        transform: translate(-50%, -50%) scale(1);
    }

    50% {
        background-color: var(--secondColor);
        transform: translate(-50%, -50%) scale(1.3);
    }

    100% {
        background-color: var(--color);
        transform: translate(-50%, -50%) scale(1);
    }
}

</style>